<template>
  <div id="sign">
    <el-transfer class="transfer"
                 :data="data"
                 target-order="push"
                 :titles="titles"
                 v-model="value"
                 @change="renewSign"></el-transfer>
    <el-button @click="orderBy(['asc', 'desc'])"
               type="warning">Ksort</el-button>
  </div>
</template>
<script>
export default {
  data() {
    return {
      data: [],
      titles: ['参数', '加密参数'],
      value: [],
      signSelect: [],
    };
  },
  created() {
    // [註冊監聽事件]
    this.$bus.$on('pass', event => {
      this.data = event;
    });
  },
  beforeDestroy: function() {
    // [銷毀監聽事件]
    // 最好在组件銷毀前，清除 specialEvent 所有監聽
    // 如果要指定哪個監聽對應方法，就需要傳入第二個參數(對應$on設定的方法)
    this.$bus.$off('pass');
  },
  methods: {
    renewSign() {
      this.signSelect = [];
      for (let i = 0; i < this.value.length; i++) {
        var idx = this.value[i] - 1;
        this.signSelect.push(this.data[idx]);
      }
      this.passSignSelect();
    },
    orderBy(orders) {
      this.signSelect = [...this.signSelect].sort((a, b) =>
        ['label', 'variable'].reduce((acc, prop, i) => {
          if (acc === 0) {
            const [p1, p2] =
              orders && orders[i] === 'desc'
                ? [b[prop], a[prop]]
                : [a[prop], b[prop]];
            acc = p1 > p2 ? 1 : p1 < p2 ? -1 : 0;
          }
          return acc;
        }, 0),
      );
      this.value.sort();
      this.passSignSelect();
    },
    passSignSelect() {
      this.$bus.$emit('emit', this.signSelect);
    },
  },
};
</script>
<style>
#sign {
  text-align: center;
  padding-top: 35px;
  padding-bottom: 36px;
  border-bottom: 1px solid #707c74;
}

#sign .el-transfer-panel__body,
#sign .el-checkbox {
  text-align: left;
}
#sign .transfer {
  margin-bottom: 30px;
}
</style>



