<template>
  <div id="formArea">
    <table cellpadding="10">
      <tr>
        <th>
          <label for="name">第三方名称 :</label>
        </th>
        <td>
          <input type="text"
                 placeholder="请填写第三方名称"
                 id="name"
                 name="name"
                 autocomplete="off"
                 v-model="third_name"
                 @input="passBasicData"
                 lazy>
        </td>
      </tr>
      <tr>
        <th>支付方式 : </th>
        <td>
          <el-select v-model="payment"
                     @change="paymentToggle">
            <el-option v-for="item in options"
                       :key="item.value"
                       :label="item.label"
                       :value="item.value"></el-option>
          </el-select>
        </td>
      </tr>
      <tr>
        <th>支付渠道 :</th>
        <td>
          <el-select v-model="platform"
                     @change="passBasicData"
                     multiple>
            <el-option v-for="item in platforms"
                       :key="item.value"
                       :label="item.label"
                       :value="item.value"></el-option>
          </el-select>
        </td>
      </tr>
      <template v-for="(item,idx) in platform">
        <tr :key="item.value">
          <th>
            <label for="formUrl">传送地址({{item}}) :</label>
          </th>
          <td>
            <input type="text"
                   autocomplete="off"
                   v-model="form_urlArr[idx]"
                   @input="formConn(item)">
          </td>
        </tr>
        <tr :key="item.value">
          <th>
            <label for="formUrl">PayType({{item}}) :</label>
          </th>
          <td>
            <input type="text"
                   autocomplete="off"
                   v-model="paytypeArr[idx]"
                   @input="formConn(item)">
          </td>
        </tr>
        <tr :key="item.value">
          <th>
            <label for="formUrl">傳值方式({{item}}) :</label>
          </th>
          <td>
            <el-select v-model="postmethodArr[idx]"
                       @change="formConn(item)">
              <el-option v-for="aa in postmethods"
                         :key="aa.label"
                         :label="aa.label"
                         :value="aa.label"></el-option>
            </el-select>
          </td>
        </tr>
        <tr :key="item.value">
          <th>
            <label for="formUrl">响应模式({{item}}) :</label>
          </th>
          <td>
            <el-select v-model="response_levelArr[idx]"
                       name="types"
                       @change="formConn(item)">
              <el-option v-for="item in types"
                         :key="item.value"
                         :label="item.label"
                         :value="item.value"></el-option>
            </el-select>
            <input type="text"
                   placeholder="第一索引值"
                   @input="formConn(item)"
                   v-model="first_keyArr[idx]">
            <input type="text"
                   placeholder="第二索引值"
                   @input="formConn(item)"
                   v-model="second_keyArr[idx]">
          </td>
        </tr>
      </template>
      <tr>
        <th>金额单位 : </th>
        <td>
          <el-select v-model="amount_unit"
                     name="amount_unit"
                     @input="passBasicData">
            <el-option v-for="fee in fees"
                       :key="fee.value"
                       :label="fee.label"
                       :value="fee.value"></el-option>
          </el-select>
        </td>
      </tr>
      <tr>
        <th>订单金额取小数点后</th>
        <td>
          <el-select v-model="decimal"
                     name="decVal"
                     @input="passBasicData">
            <el-option v-for="count in counts"
                       :key="count.value"
                       :label="count.label"
                       :value="count.value"></el-option>
          </el-select>
        </td>
      </tr>
    </table>
  </div>
</template>
<script>
export default {
  name: 'formArea',
  data() {
    return {
      options: [
        { label: '网银', value: 'wy' },
        { label: '微信', value: 'wx' },
        { label: '支付宝', value: 'zfb' },
        { label: 'QQ', value: 'qq' },
      ],
      platform: [],
      platforms: [],
      counts: [
        { label: '0位', value: 0 },
        { label: '1位', value: 1 },
        { label: '2位', value: 2 },
      ],
      fees: [{ label: '元', value: 1 }, { label: '分', value: 2 }],
      // 选择
      third_name: '', //第三方支付名稱
      payment: '', //支付渠道
      form_urlArr: [], // 傳送地址阵列
      form_url: '', // 傳送地址
      paytypeArr: [],
      paytype: '',
      postmethod: '',
      postmethodArr: [],
      postmethods: [
        {
          label: 'CURL-POST',
          value: 'CURL-POST',
        },
        {
          label: 'CURL-GET',
          value: 'CURL-GET',
        },
        {
          label: 'HEADER-POST',
          value: 'HEADER-POST',
        },
        {
          label: 'HEADER-GET',
          value: 'HEADER-GET',
        },
      ],
      amount_unit: '', // 單位
      decimal: '', // 小數點
      basicData: {},
      types: [
        {
          label: '一层',
          value: 1,
        },
        {
          label: '直接跳转',
          value: 0,
        },
        {
          label: '两层',
          value: 2,
        },
      ],
      type: '',
      first_keyArr: [],
      first_key: '',
      second_keyArr: [],
      second_key: '',
      response_levelArr: [],
      response_level: '',
    };
  },
  methods: {
    paymentToggle() {
      if (this.payment === 'wy') {
        this.platforms = [
          { label: '网银手机', value: 'wyh5' },
          { label: '网银直连', value: 'wylk' },
          { label: '银联快捷', value: 'ylkj' },
          { label: '银联快捷手机', value: 'ylkjh5' },
          { label: '银联扫码', value: 'ylbs' },
          { label: '银联手机', value: 'ylh5' },
        ];
      } else if (this.payment === 'wx') {
        this.platforms = [
          { label: '微信扫码', value: 'wxbs' },
          { label: '微信手机', value: 'wxh5' },
          { label: '微信反扫', value: 'wxfs' },
          { label: '京东扫码', value: 'jdbs' },
          { label: '京东手机', value: 'jdh5' },
          { label: '京东反扫', value: 'jdfs' },
          { label: '百度扫码', value: 'bdbs' },
          { label: '百度手机', value: 'bdh5' },
          { label: '百度反扫', value: 'bdfs' },
          { label: 'QQ扫码', value: 'qqbs' },
          { label: 'QQ手机', value: 'qqh5' },
          { label: 'QQ反扫', value: 'qqfs' },
        ];
      } else if (this.payment === 'zfb') {
        this.platforms = [
          { label: '支付宝扫码', value: 'zfbbs' },
          { label: '支付宝手机', value: 'zfbh5' },
          { label: '支付宝反扫', value: 'zfbfs' },
        ];
      } else if (this.payment === 'qq') {
        this.platforms = [
          { label: 'QQ扫码', value: 'qqbs' },
          { label: 'QQ手机', value: 'qqh5' },
          { label: 'QQ反扫', value: 'qqfs' },
        ];
      } else {
        this.platforms = [];
      }

      this.formConn();
    },
    formConn() {
      // this.form_url = this.form_urlArr.join(`^_^`);
      this.form_url = '';
      for (let i = 0; i < this.form_urlArr.length; i++) {
        this.form_url += `${this.platform[i]}^_^${this.form_urlArr[i]}>_<`;
      }
      this.paytype = '';
      for (let i = 0; i < this.paytypeArr.length; i++) {
        this.paytype += `${this.platform[i]}^_^${this.paytypeArr[i]}>_<`;
      }

      this.postmethod = '';
      for (let i = 0; i < this.postmethodArr.length; i++) {
        this.postmethod += `${this.platform[i]}^_^${this.postmethodArr[i]}>_<`;
      }

      this.first_key = '';
      for (let i = 0; i < this.first_keyArr.length; i++) {
        this.first_key += `${this.platform[i]}^_^${this.first_keyArr[i]}>_<`;
      }

      this.second_key = '';
      for (let i = 0; i < this.second_keyArr.length; i++) {
        this.second_key += `${this.platform[i]}^_^${this.second_keyArr[i]}>_<`;
      }

      this.response_level = '';
      for (let i = 0; i < this.response_levelArr.length; i++) {
        this.response_level += `${this.platform[i]}^_^${
          this.response_levelArr[i]
        }>_<`;
      }
      this.passBasicData();
    },
    passBasicData() {
      this.basicData = {
        third_name: this.third_name,
        payment: this.payment,
        platform: this.platform,
        form_url: this.form_url,
        paytype: this.paytype,
        postmethod: this.postmethod,
        amount_unit: this.amount_unit,
        decimal: this.decimal,
        first_key: this.first_key,
        second_key: this.second_key,
        response_level: this.response_level,
      };
      this.$bus.$emit('emit2PHPdata1', this.basicData);
    },
  },
};
</script>
<style lang="scss" scoped>
#formArea {
  padding-bottom: 15px;
  padding-left: 20px;
  border-bottom: 1px solid #707c74;
  padding-top: 30px;
}

#formArea table {
  width: 100%;
}
</style>


