<template>
  <div id="app">
    <h1><img src="../static/logoWhite.png"></h1>
    <!-- ↓ 組件 ↓ -->
    <div id="left">
      <parameter></parameter>
      <addparameter></addparameter>
      <sign2></sign2>
      <passValue></passValue>
    </div>
    <div id="right">
      <varList></varList>
      <sign></sign>
      <encryption></encryption>
    </div>
  </div>
</template>
<script>
import parameter from './components/parameter';
import addparameter from './components/addparameter';
import sign2 from './components/sign2';
import passValue from './components/passValue';
import varList from './components/varList';
import sign from './components/sign';
import encryption from './components/encryption';

export default {
  name: 'App',
  components: {
    parameter,
    addparameter,
    sign2,
    passValue,
    varList,
    sign,
    encryption,
  },
};
</script>
<style>
#app {
  font-size: 0;
  font-family: 'Microsoft YaHei', Tahoma, Arial, Helvetica, sans-serif;
  box-sizing: border-box;
}
h1 {
  background-color: #a30000;
  text-align: center;
  padding-top: 30px;
  padding-bottom: 30px;
}
img {
  width: 300px;
}
#left,
#right {
  width: 50%;
  display: inline-block;
  font-size: 22px;
  vertical-align: top;
}

#left {
  background-color: #f0f1f9;
  color: #373c38;
}
#right {
  background: #f1fee5;
}

textarea {
  resize: none;
  outline: none;
  margin-top: 8px;
}

input[type='text'] {
  padding: 4px 5px;
  margin-bottom: 4px;
  border: 1px #bdc0ba solid;
}

input,
select {
  outline: none;
}

input[type='text'] {
  text-indent: 6px;
}

select {
  padding: 4px 5px;
}
</style>


