<template>
  <div id="sign2">
    <el-transfer class="transfer"
                 :data="data"
                 target-order="push"
                 :titles="titles"
                 v-model="value"
                 @change="renewSign"></el-transfer>
    <table cellpadding="10">
      <tr>
        <th>响应成功索引值 : </th>
        <td colspan="2">
          <input type="text"
                 placeholder="响应成功代码"
                 @input="passType"
                 v-model="resCode">
          <input type="text"
                 placeholder="响应成功訊息"
                 @input="passType"
                 v-model="resMsg">
        </td>
      </tr>
      <tr>
        <th>响应错误索引值 : </th>
        <td colspan="2">
          <input type="text"
                 placeholder="响应错误代码索引"
                 @input="passType"
                 v-model="resErrCode">
          <input type="text"
                 placeholder="响应錯誤訊息索引"
                 @input="passType"
                 v-model="resErrMsg">
        </td>
      </tr>
      <!-- <tr>
        <th>响应模式 : </th>
        <td>
          <el-select v-model="type"
                     name="types"
                     @change="writeResponse_key">
            <el-option v-for="item in types"
                       :key="item.label"
                       :label="item.label"
                       :value="item.value"></el-option>
          </el-select>
        </td>
        <td>
          <input type="text"
                 placeholder="第一索引值"
                 @input="writeResponse_key"
                 v-model="QRCode1">
          <input type="text"
                 placeholder="第二索引值"
                 @input="writeResponse_key"
                 v-model="QRCode2">
        </td>
      </tr> -->
    </table>
  </div>
</template>
<script>
export default {
  data() {
    return {
      data: [],
      titles: ['参数', '传值参数'],
      value: [],
      signSelect: [],
      // types: [
      //   {
      //     label: '一层',
      //     value: 1,
      //   },
      //   {
      //     label: '直接跳转',
      //     value: 0,
      //   },
      //   {
      //     label: '两层',
      //     value: 2,
      //   },
      // ],
      // type: '',
      response_key: [],
      res: {},
      resCode: '',
      resErrCode: '',
      resMsg: '',
      resErrMsg: '',
      // QRCode1: '',
      // QRCode2: '',
    };
  },
  created() {
    // [註冊監聽事件]
    this.$bus.$on('pass', event => {
      this.data = event;
    });
  },
  beforeDestroy: function() {
    // [銷毀監聽事件]
    // 最好在组件銷毀前，清除 specialEvent 所有監聽
    // 如果要指定哪個監聽對應方法，就需要傳入第二個參數(對應$on設定的方法)
    this.$bus.$off('pass');
  },
  methods: {
    renewSign() {
      this.signSelect = [];
      for (let i = 0; i < this.value.length; i++) {
        var idx = this.value[i] - 1;
        this.signSelect.push(this.data[idx]);
      }
      this.passSignSelect();
    },
    passSignSelect() {
      this.$bus.$emit('emit2PHP', this.signSelect);
    },
    passType() {
      this.res = {};
      this.res = {
        Success_key: this.resCode,
        Success_value: this.resMsg,
        Error_No: this.resErrCode,
        Error_Msg: this.resErrMsg,
        response_key: this.response_key,
      };
      this.$bus.$emit('emit2PHPdata2', this.res);
    },
  },
};
</script>
<style lang="scss">
#sign2 {
  background-color: #ecc1d0;
  text-align: center;
  padding-top: 35px;
  padding-bottom: 36px;
  border-bottom: 1px solid #707c74;
  table {
    margin: 0 auto;
    tr td {
      text-align: left;
    }
  }
}

#sign2 .el-transfer-panel__body,
#sign2 .el-checkbox {
  text-align: left;
}

#sign2 .transfer {
  margin-bottom: 30px;
}
</style>



