<template>
  <div>
    <h2>默认变数</h2>
    <el-table :data="tableData"
              stripe
              style="width: 100%">
      <el-table-column prop="id"
                       label="#"
                       width="180">
      </el-table-column>
      <el-table-column prop="name"
                       label="变数名称"
                       width="180">
      </el-table-column>
      <el-table-column prop="var"
                       label="变数意义">
      </el-table-column>
    </el-table>
  </div>

</template>
<script>
export default {
  data() {
    return {
      tableData: [
        {
          id: '1',
          name: '商户号',
          var: '$pay_mid',
        },
        {
          id: '2',
          name: '商户订单号',
          var: '$order_no',
        },
        {
          id: '3',
          name: '订单金额',
          var: '$MOAmount',
        },
        {
          id: '4',
          name: '异步通知URL',
          var: '$notify_url',
        },
        {
          id: '5',
          name: '同步通知地址',
          var: '$return_url',
        },
        {
          id: '6',
          name: '用户端IP位址',
          var: '$client_ip',
        },
        {
          id: '7',
          name: '订单时间',
          var: '$order_time',
        },
        {
          id: '8',
          name: '银行代码',
          var: '$band_code',
        },
        {
          id: '9',
          name: '通道名称',
          var: '$paytype',
        },
      ],
    };
  },
};
</script>
<style lang="scss" scoped>
div {
  padding-top: 30px;
  background-color: #fff;
  h2 {
    margin-top: 0;
    text-align: center;
  }
}
</style>



