// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import App from './App';
// 引用element-ui
import {
  Transfer,
  Radio,
  Select,
  Option,
  Button,
  RadioGroup,
  RadioButton,
  Checkbox,
  Dialog,
  MessageBox,
  Table,
  TableColumn,
} from 'element-ui';
Vue.use(Transfer);
Vue.use(Radio);
Vue.use(Select);
Vue.use(Option);
Vue.use(Button);
Vue.use(RadioGroup);
Vue.use(RadioButton);
Vue.use(Checkbox);
Vue.use(Dialog);
Vue.use(Table);
Vue.use(TableColumn);
// 引用完成
// 引用eventBus
import eventBus from './eventBus';
// 引用完成
// 引用axios
import axios from 'axios';
Vue.prototype.$http = axios;
// 引用完成
Vue.prototype.$msgbox = MessageBox;
Vue.prototype.$confirm = MessageBox.confirm;
new Vue({
  el: '#app',
  data() {
    return {
      bus: eventBus,
    };
  },
  components: { App },
  template: '<App/>',
});



