<template>
  <div id="passValue">
    <table cellpadding="10">
      <!-- <tr>
        <th>传值方式 : </th>
        <td>
          <el-radio v-model="radio"
                    label="CURLPOST"
                    @change="buildJson">CURL-POST</el-radio>
          <el-radio v-model="radio"
                    label="CURLGET"
                    @change="buildJson">CURL-GET</el-radio>
          <el-radio v-model="radio"
                    label="HEADERPOST"
                    @change="buildJson">HEADER-POST</el-radio>
          <el-radio v-model="radio"
                    label="HEADERGET"
                    @change="buildJson">HEADER-GET</el-radio>
          <el-radio v-model="radio"
                    label="ALLHAVE"
                    @change="buildJson">ALLHAVE</el-radio>
        </td>
      </tr> -->
      <tr>
        <th>传值结构 : </th>
        <td>
          <el-select v-model="passFormat"
                     name="passFormat"
                     @change="buildJson">
            <el-option v-for="format in passFormats"
                       :key="format.value"
                       :label="format.label"
                       :value="format.label"></el-option>
          </el-select>
        </td>
      </tr>
      <tr>
        <th>响应结构 : </th>
        <td>
          <el-select v-model="respFormat"
                     name="respFormat"
                     @change="buildJson">
            <el-option v-for="format in respFormats"
                       :key="format.value"
                       :label="format.label"
                       :value="format.label"></el-option>
          </el-select>
        </td>
      </tr>
    </table>
    <el-button type="success"
               @click="creatPHP()"
               :disabled="loading">生成</el-button>
  </div>
</template>
<script>
export default {
  name: 'passValue',
  data() {
    return {
      passFormats: [
        { label: 'JSON' },
        { label: 'XML' },
        { label: 'xmlCDATA' },
        { label: 'HTML' },
      ],
      respFormats: [
        { label: 'JSON' },
        { label: 'XML' },
        { label: 'xmlCDATA' },
        { label: 'HTML' },
      ],
      // radio: 'CURLPOST', // 傳值方式
      passFormat: '', // 傳值格式
      respFormat: '', // 響應結構
      passBasicData: {}, // 傳送支付基本資料
      passParams: [], // 傳送params(陣列格式)
      passParamsObj: {}, // 傳送params(物件格式)
      passRes: {}, // 傳送響應錯誤訊息參數
      passSign: {}, // 傳送簽名格式
      passJsObj: {}, // 傳送至php的Json物件
      loading: false,
    };
  },
  created() {
    // [註冊監聽事件]
    this.$bus.$on('emit2PHP', event => {
      this.passParams = event;
      this.buildJson();
    });
    this.$bus.$on('emit2PHPdata1', event => {
      this.passBasicData = event;
      this.buildJson();
    });
    this.$bus.$on('emit2PHPdata2', event => {
      this.passRes = event;
      this.buildJson();
    });
    this.$bus.$on('signPass', event => {
      this.passSign = event;
      try {
        this.passSign = JSON.parse(this.passSign);
      } catch (error) {
        this.passSign = this.passSign;
      }
      this.buildJson();
    });
  },
  beforeDestroy: function() {
    // [銷毀監聽事件]
    // 最好在组件銷毀前，清除 specialEvent 所有監聽
    // 如果要指定哪個監聽對應方法，就需要傳入第二個參數(對應$on設定的方法)
    this.$bus.$off('emit2PHP');
    this.$bus.$off('emit2PHPdata1');
    this.$bus.$off('emit2PHPdata2');
    this.$bus.$off('signPass');
  },
  methods: {
    buildJson() {
      for (let i = 0; i < this.passParams.length; i++) {
        let label = this.passParams[i].label;
        let variable = this.passParams[i].variable;
        this.passParamsObj[label] = variable;
      }
      this.passParamsObj['sign'] = this.passSign;
      this.passParamsObj = JSON.stringify(this.passParamsObj, null, 2);
      try {
        this.passParamsObj = JSON.parse(this.passParamsObj, null, 2);
      } catch (error) {}
      this.passJsObj = {
        third_name: this.passBasicData.third_name,
        payment: this.passBasicData.payment,
        platform: this.passBasicData.platform,
        Success_key: this.passRes['Success_key'],
        Success_value: this.passRes['Success_value'],
        Error_No: this.passRes['Error_No'],
        Error_Msg: this.passRes['Error_Msg'],
        form_url: this.passBasicData.form_url,
        paytype: this.passBasicData.paytype,
        postmethod: this.passBasicData.postmethod,
        first_key: this.passBasicData.first_key,
        second_key: this.passBasicData.second_key,
        response_level: this.passBasicData.response_level,
        res_structure: this.respFormat,
        req_structure: this.passFormat,
        amount_unit: this.passBasicData.amount_unit,
        decimal: this.passBasicData.decimal,
        params: this.passParamsObj,
      };
      console.log(JSON.stringify(this.passJsObj, null, 2));
    },
    creatPHP() {
      (this.loading = true), //停用按鈕
        this.$http
          .post('./php/jsontophp.php', {
            data: JSON.stringify(this.passJsObj),
          })
          .then(res => {
            (this.loading = false), //啟用按鈕
              console.log(res);
            // 建立隱藏的可下載連結
            var eleLink = document.createElement('a');
            eleLink.download = 'post.php';
            eleLink.style.display = 'none';
            // 字元內容轉變成blob地址
            var blob = new Blob([res.data]);
            eleLink.href = URL.createObjectURL(blob);
            // 觸發點選
            document.body.appendChild(eleLink);
            eleLink.click();
            // 然後移除
            document.body.removeChild(eleLink);
          })
          .catch(err => {
            console.log(err);
          });
    },
  },
};
</script>
<style lang="scss" scoped>
#passValue {
  background-color: #ecc1d0;
  padding-top: 15px;
  padding-bottom: 40px;
  text-align: center;
}

#passValue table {
  margin: 0 auto;
}

#passValue .option {
  color: #1c1c1c;
  cursor: pointer;
}
</style>


