<template>
  <div id="encryption">
    <div>
      <table cellpadding="10">
        <tr>
          <th colspan="4">
            sign拼接方式 :
          </th>
        </tr>
        <tr>
          <th>
            <el-checkbox v-model="havekey"
                         size="large"
                         @change="isRSA">加入索引</el-checkbox>
            $key
          </th>
          <td>
            <input type="text"
                   placeholder="预设为无key值"
                   class="encodeArr"
                   v-model="midConn"
                   @input="reStr">
          </td>
          <th>
            $value
          </th>
          <td>
            <input type="text"
                   placeholder="预设为无间隔"
                   class="encodeArr"
                   v-model="lastConn"
                   @input="reStr">
          </td>
        </tr>
        <tr>
          <th colspan="4">
            <el-checkbox v-model="checked"
                         size="large"
                         @change="isRSA">是否加入密鑰</el-checkbox>
            <el-radio v-show="checked"
                      v-model="keyType"
                      label="$pu_key"
                      value='$pu_key'
                      @change="reStr">公钥</el-radio>
            <el-radio v-show="checked"
                      v-model="keyType"
                      label="$pr_key"
                      value='$pr_key'
                      @change="reStr">私钥</el-radio>
          </th>
        </tr>
        <tr>
          <th colspan="2"
              v-if="checked">
            密鑰拼接：
          </th>
          <td colspan="2"
              v-if="checked">
            <input type="text"
                   @input="reStr"
                   style="width:30px"
                   v-model="keyConn">
            <input type="text"
                   @input="reStr"
                   style=""
                   v-model="keyName">
            <input type="text"
                   @input="reStr"
                   style="width:30px"
                   v-model="keyConn2">
          </td>
        </tr>
        <tr v-for="(item,idx) in format"
            :key="idx">
          <th colspan="2">
            加密方式 :
          </th>
          <td colspan="2">
            <el-select v-model="format[idx]"
                       @change="reStr">
              <el-option v-for="item in formats"
                         :key="item.value"
                         :label="item.label"
                         :value="item.value"></el-option>
            </el-select>
            <el-button @click="addOne"
                       v-if="idx<1"
                       type="primary"
                       icon="el-icon-plus"
                       circle></el-button>
            <el-button @click="removeOne(idx)"
                       v-else
                       type="primary"
                       icon="el-icon-minus"
                       circle></el-button>
          </td>
        </tr>
      </table>
      <div class="textArea">
        <textarea name=""
                  id="text1"
                  cols="60"
                  rows="6"
                  v-model="signParam"></textarea>
      </div>
      <div class="textArea">
        <textarea name=""
                  id="text2"
                  cols="60"
                  rows="6"></textarea>
        <i class="el-icon-document copyIcon"
           @click="copy"></i>
        <i class="el-icon-zoom-in zoomIcon"
           @click="dialogVisible = true"></i>
        <el-dialog title="JSON字串"
                   :visible.sync="dialogVisible"
                   width="30%"
                   :before-close="handleClose">
          <div style="width:80%">{{signArr}}</div>
          <span slot="footer"
                class="dialog-footer">
            <el-button type="primary"
                       @click="dialogVisible = false">确 定</el-button>
          </span>
        </el-dialog>
      </div>
    </div>
  </div>
</template>
<script>
let encodeAgain = {};
export default {
  name: 'response',
  data() {
    return {
      midConn: '',
      lastConn: '',
      formats: [
        { label: 'MD5', value: 'MD5' },
        { label: 'SHA', value: 'SHA' },
        { label: 'base64', value: 'base64' },
        { label: 'urlencode', value: 'urlencode' },
        { label: 'AES', value: 'AES' },
        { label: 'JSON', value: 'JSON' },
        { label: 'xmlCDATA', value: 'xmlCDATA' },
        { label: 'HTML', value: 'HTML' },
        { label: '大写', value: 'upper' },
        { label: '小写', value: 'lower' },
        // { label: 'RSA公钥' },
        // { label: 'RSA私钥' },
      ],
      keyType: '$pu_key',
      keyConn: '&',
      keyConn2: '=',
      keyName: 'key',
      format: ['MD5'],
      signSelect: [], //接過來需要簽名的陣列
      signParam: '', //字串連接示意
      signArr: '', //要傳給程式的陣列
      checked: true,
      havekey: true,
      dialogVisible: false,
    };
  },
  created() {
    // [註冊監聽事件]
    this.$bus.$on('emit', event => {
      this.signSelect = event;
      this.reStr();
    });
  },
  beforeDestroy: function() {
    // [銷毀監聽事件]
    // 最好在组件銷毀前，清除 specialEvent 所有監聽
    // 如果要指定哪個監聽對應方法，就需要傳入第二個參數(對應$on設定的方法)
    this.$bus.$off('emit');
  },
  methods: {
    addOne() {
      this.format.push('MD5');
      this.signIt();
    },
    removeOne(idx) {
      this.format.splice(idx, 1);
      this.signIt();
    },
    copy() {
      let copyArea = document.querySelector('#text2');
      copyArea.select();
      try {
        if (document.execCommand('copy', false, null)) {
          document.execCommand('copy');
        } else alert('複製失敗');
      } catch (err) {
        alert('複製失敗');
      }
      copyArea.blur();
    },
    isRSA() {
      if (this.checked == false) {
        this.formats.push(
          { label: 'RSA公钥', value: 'RSApu' },
          { label: 'RSA私钥', value: 'RSApr' },
        );
      }
      if (this.checked == true) {
        this.formats.pop();
        this.formats.pop();
      }
      this.reStr();
    },
    reStr() {
      this.signParam = '';
      if (this.havekey === true) {
        for (let i = 0; i < this.signSelect.length; i++) {
          this.signParam += `${this.signSelect[i].label}${this.midConn}${
            this.signSelect[i].variable
          }${this.lastConn}`;
        }
      } else if (this.havekey === false) {
        for (let i = 0; i < this.signSelect.length; i++) {
          this.signParam += `${this.midConn}${this.signSelect[i].variable}${
            this.lastConn
          }`;
        }
      }
      this.signParam = this.signParam.substring(
        0,
        this.signParam.length - this.lastConn.length,
      );
      if (this.checked) {
        this.signParam += `${this.keyConn}${this.keyName}${this.keyConn2}${
          this.keyType
        }`;
      }
      this.signIt();
    },
    signIt() {
      this.signArr = '{"str_arr":{';
      for (let i = 0; i < this.signSelect.length; i++) {
        this.signArr += `"${this.signSelect[i].label}":"${
          this.signSelect[i].variable
        }",`;
      }
      this.signArr = this.signArr.substring(0, this.signArr.length - 1);
      this.signArr += `},"mid_conn":"${this.midConn}","last_conn":"${
        this.lastConn
      }","encrypt":[`;
      for (let i = 0; i < this.format.length; i++) {
        this.signArr += `"${this.format[i]}",`;
      }
      this.signArr = this.signArr.substring(0, this.signArr.length - 1);
      this.signArr += `],"havekey":${this.havekey}`;
      if (this.checked == true) {
        this.signArr += `,"key_str":"${this.keyConn}${this.keyName}${
          this.keyConn2
        }","key":"${this.keyType}"}`;
      } else {
        this.signArr += `,"key_str":""}`;
      }
      let text2 = document.querySelector('#text2');
      text2.value = this.signArr;
      this.passParams();
    },
    passParams() {
      this.$bus.$emit('signPass', this.signArr);
    },
    handleClose(done) {
      this.$confirm('确认关闭？')
        .then(_ => {
          done();
        })
        .catch(_ => {});
    },
  },
};
</script>
<style lang="scss" scoped>
#encryption {
  padding-bottom: 30px;
  padding-top: 30px;
  .choose {
    text-align: center;
    margin-bottom: 40px;
  }
  table {
    margin: 0 auto;
  }
}

#encryption .textArea {
  position: relative;
}

#encryption .textArea textarea {
  display: block;
  margin: 30px auto 0;
}

#encryption .textArea .copyIcon {
  position: absolute;
  right: 31%;
  top: 2%;
  cursor: pointer;
}
.zoomIcon {
  position: absolute;
  right: 31%;
  bottom: 2%;
  cursor: pointer;
}

input.encodeArr {
  width: 100px;
}
</style>


